.ind-headers {
	width: 100%;
	background: #fff3f0;
	position: relative;
	overflow: hidden;
	&::after {
		content: "";
		position: absolute;
		width: 577px;
		height: 577px;
		background: #ffc178;
		opacity: 0.8;
		border-radius: 50%;
		top: 50%;
		left: -300px;
		transform: translate(0%, -50%);
		filter: blur(300px);
		z-index: 2;
	}
	&__wrapper {
		position: relative;
		padding: 25px 0 120px 0;
		z-index: 5;
		display: flex;
		align-items: flex-start;
		width: 100%;
		justify-content: space-between;
		&::after {
			content: "";
			position: absolute;
			width: 25px;
			height: 25px;
			top: 0;
			left: 0;
			background: url("./../svg/headers/decor-1.svg") no-repeat;
		}
	}
	&__content {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		justify-content: flex-start;
		padding: 77px 0 0 0;
		&-t {
			font-family: NEXTART;
			width: 510px;
			font-style: normal;
			font-weight: bold;
			font-size: 42px;
			line-height: 68px;
			color: #333333;
			position: relative;
			&::after {
				content: "";
				position: absolute;
				top: -10px;
				right: -10px;
				width: 214px;
				height: 79px;
				background: url("./../svg/headers/decor-2.svg") no-repeat;
			}
			&::before {
				content: "";
				position: absolute;
				bottom: -5px;
				right: -2px;
				width: 366px;
				height: 12px;
				background: url("./../svg/headers/decor-3.svg") no-repeat;
			}
		}
		&-d {
			margin: 37px 0 0 0;
			width: 510px;
			font-family: TTNorms;
			font-style: normal;
			font-weight: 500;
			font-size: 20px;
			line-height: 28px;
			color: #333333;
		}
		&-btns {
			display: flex;
			align-items: center;
			justify-content: flex-start;
			margin: 46px 0 0 0;
			gap: 30px;
		}
		&-b {
			padding: 16px 40px;
			background: #ffffff;
			border: 2px solid #4f6bfd;
			border-radius: 100px;
			display: flex;
			align-items: center;
			justify-content: center;
			gap: 13px;
			cursor: pointer;
			margin: 0 20px 0 0;
			& p {
				font-family: TT Norms;
				font-style: normal;
				font-weight: 500;
				font-size: 18px;
				line-height: 120%;
				letter-spacing: 0.02em;
				color: #4f6bfd;
			}
		}
		&-wb {
			display: flex;
			align-items: center;
			justify-content: center;
			gap: 15px;
			& p {
				font-family: OpenSans;
				font-style: normal;
				font-weight: 300;
				font-size: 15px;
				line-height: 20px;
				color: #000000;
			}
		}
		&-ozon {
			display: flex;
			align-items: center;
			justify-content: center;
			gap: 15px;
			& p {
				font-family: OpenSans;
				font-style: normal;
				font-weight: 300;
				font-size: 15px;
				line-height: 20px;
				color: #000000;
			}
		}
		&-l {
			display: flex;
			align-items: center;
			justify-content: flex-start;
			gap: 40px;
			margin: 95px 0 0 0;
		}
		&-i {
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			justify-content: flex-start;
			width: 150px;
			gap: 6px;
			& h3 {
				font-family: DMSans;
				font-style: normal;
				font-weight: bold;
				font-size: 30px;
				color: #161616;
			}
			& p {
				font-family: TT Norms;
				font-style: normal;
				font-weight: normal;
				font-size: 15px;
				line-height: 20px;
				color: #333333;
			}
		}
		&-ln {
			width: 1px;
			height: 111px;
			background: #cecece;
		}
	}
	&__banner {
		position: absolute;
		top: 32px;
		right: -400px;
		width: 832px;
		height: 550px;
		border: 3px solid #ffeab9;
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 43px;
		padding: 44px 41px;
		&-d {
			position: absolute;
			display: flex;
			align-items: center;
			gap: 6px;
			justify-content: center;
			top: 20px;
			left: 41px;
		}
		&-i {
			width: 9px;
			height: 9px;
			background: #fdbd28;
			border-radius: 50%;
		}
		&-p {
			width: 100%;
			height: 100%;
		}
	}
}

